.hero {
  min-height: 100vh;
  width: 100%;

  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  color: white;
  text-align: center;

  animation: fadeIn 1.2s ease-in-out;
}

.hero h1 {
  font-size: 3rem;
  margin-bottom: 0.5rem;
}

.hero p {
  font-size: 1.3rem;
  opacity: 0.9;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(25px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Mobile */
@media (max-width: 768px) {
  .hero h1 {
    font-size: 2.2rem;
  }
  .hero p {
    font-size: 1.1rem;
  }
}

.education-section {
  padding: 60px 20px;
  background-color: #ffffff;
  color: #2d3436;
}

.container {
  max-width: 900px;
  margin: 0 auto;
}

.section-title {
  font-size: 2rem;
  margin-bottom: 40px;
  border-bottom: 2px solid #636e72;
  display: inline-block;
  padding-bottom: 5px;
}

.edu-item {
  margin-bottom: 30px;
  padding-left: 20px;
  border-left: 3px solid #003366; /* Virginia Tech Maroon or a Professional Blue */
}

.edu-header {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

.edu-header h3 {
  margin: 0;
  font-size: 1.25rem;
  color: #003366;
}

.date {
  font-weight: bold;
  color: #636e72;
}

.institution {
  margin: 5px 0 0 0;
  font-weight: 500;
  font-size: 1.1rem;
}

.location {
  margin: 2px 0 0 0;
  color: #b2bec3;
  font-style: italic;
}